@import "src/components/variables";

.home-page {
  .text-zone {
    transform: translateY(-50%);
    max-height: 90%;
    margin-left: 10px;
    width: 40%;
    position: absolute;
    left: 10%;
    top: 50%;
  }

  h1 {
    color: #fff;
    font-size: 35px;
    line-height: 53px;
    margin: 0;
    font-family: 'Coolvetica';
    font-weight: 400;

    @media (min-width: 900px) {
      font-size: 56px;
    }

    &:before {
      content: '<h1>';
      font-family: 'La Belle Aurore', cursive;
      color: $primary-color;
      font-size: 18px;
      position: absolute;
      margin-top: -40px;
      left: -15px;
      opacity: 0.6;
    }

    &:after {
      content: '<h1/>';
      font-family: 'La Belle Aurore', cursive;
      color: $primary-color;
      font-size: 18px;
      position: absolute;
      margin-top: 18px;
      margin-left: 20px;
      animation: fadeIn 1s 1.7s backwards;
      opacity: 0.6;
    }

    img {
      margin: 0 -10px 0 20px;
      opacity: 0;
      width: 50px;
      height: auto;
      animation: rotateIn 1s linear both;
      animation-delay: 1.4s;
    }
  }

  h2 {
    color: black;
    margin-top: 20px;
    font-weight: 400;
    font-size: 11px;
    font-family: sans-serif;
    letter-spacing: 3px;
    animation: fadeIn 1s 1.8s backwards;
  }
}

.flat-button {
  color: white;
  background: mediumpurple;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 4px;
  font-family: sans-serif;
  text-decoration: none;
  padding: 10px 18px;
  border: 1px solid;
  margin-top: 25px;
  float: left;
  animation: fadeInAnimation 1s 1.8s backwards;
  white-space: nowrap;
  appearance: none;
  box-shadow: 0 0 3px 1px;
  cursor: pointer;

  &:hover {
    background: white;
    color: $primary-color;
  }
}

#dark {
  & .home-page h2 {
    color: purple;
  }

  & .flat-button {
    background: black;
  }

  & .flat-button:hover {
    background: black;
  }
}


@media screen and (max-width: 1200px) {
  .tags, .home-page h1::before,
  .home-page h1::after {
    display: none;
  }

  .flat-button {
    float: none;
    display: block;
    margin: 20px auto 3px auto;
    width: 155px;
  }

  .home-page {
    .text-zone {
      position: initial;
      width: 100%;
      transform: none;
      padding: 10px;
      box-sizing: border-box;
    }
  }
}


