@import "src/components/variables";

.quiz {
  position: relative;
  width: 500px;
  border-radius: 30px;
  padding: 40px;
  background-color: #fff;

  .result {
    text-align: center;

    button {
      font-weight: bold;
      font-family: 'Nunito', sans-serif;
      border-radius: 30px;
      border: 0;
      padding: 15px 30px;
      font-size: 20px;
      background-color: $primary-color;
      color: #fff;
      margin-top: 20px;
      cursor: pointer;
    }

    h2 {
      margin-bottom: 0;
    }

    img {
      width: 150px;
    }
  }

  .progress {
    height: 10px;
    border-radius: 30px;
    background-color: rgb(232, 232, 232);
    margin-bottom: 25px;

    &__inner {
      height: 100%;
      border-radius: 30px;
      width: 80%;
      background: $primary-color;
      transition: all 0.3s ease-in-out;
      background: linear-gradient(90deg, $primary-color 0%, rgba(0, 212, 255, 1) 100%);
    }
  }

  &:before,
  &:after {
    content: '';
    display: block;
    height: 50px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  &:before {
    width: 95%;
    bottom: -10px;
    background-color: rgba(255, 255, 255, 0.8);
    z-index: 0;
  }

  &:after {
    width: 90%;
    bottom: -20px;
    background-color: rgba(255, 255, 255, 0.3);
    z-index: 1;
  }

  h1 {
    margin-bottom: 15px;
  }

  ul {
    list-style: none;
    padding: 0;

    li {
      padding: 15px;
      border: 2px solid rgba(0, 0, 0, 0.1);
      border-radius: 15px;
      margin-bottom: 10px;
      cursor: pointer;
      transition: all 0.1s ease-in-out;

      &:hover {
        border: 2px solid rgba(0, 0, 0, 0.3);
      }
    }
  }
}

#dark {
  & .quiz {
    background: #2E2E2E;
    box-shadow: 0 0 3px 2px;
    color: white;
  }

  & .quiz::before,
  & .quiz::after {
    background: #2E2E2E;
    box-shadow: 0px 7px 3px -3px white;
  }

  & .quiz ul li {
    border: 1px solid white;
    box-shadow: 0 0 3px 1px;
  }
}

@media screen and (max-width: 500px){
  .quiz {
    width: 350px;
    padding: 40px 20px;
  }
}