$cat-gray: #7c1e8f;
$cat-gray-dark: #4c0e59;
$cat-gray-light: #570d66;
$cat-gray-white: #9332a8;

$crab-red: #f02630;
$crab-red-light: #db1d27;
$crab-red-dark: #912127;

.position-fox {
  position: absolute;
  right: 0;
  margin-top: -65px;
}

.wrapper {
  width: 50vw;
  height: 50vw;
  max-width: 100vh;
  max-height: 100vh;
}

.pl {
  width: 100%;
  height: 100%;
  position: absolute;

  &:hover {
    opacity: 0.5;
  }
}

.castShadow {
  position: relative;
  right: 0;
  height: 40px;
  width: 320px;
  background-color: black;
  filter: blur(10px);
  border-radius: 50%;
  opacity: 0.45;
  animation: animateShadow 3s infinite alternate cubic-bezier(0.93, 0, 0.27, 1);
}

#dark .castShadow {
  background: white;
}

@for $i from 1 to 21 {
  .wrapper .pl:nth-child(#{$i}) {
    animation: transform#{$i} 3s infinite alternate cubic-bezier(0.93, 0, 0.27, 1);
  }
}
