@media screen and (max-width: 1200px){
  .position-fox {
    position: initial;
    margin: 0 auto;
  }

  .wrapper {
    height: 55vh;
    position: relative;
    margin: auto;
  }

  .castShadow {
    position: relative;
    margin: auto;
  }
}
