@import "src/components/variables";

.nav-bar {
  position: fixed;
  background: #181818;
  width: 60px;
  height: 100%;
  top: 0;
  z-index: 3;
  min-height: 500px;

  .logo {
    display: block;
    padding: 8px 0;

    img {
      display: block;
      margin: auto;
      width: 40px;
      height: auto;
    }

    .sub-logo {
      color: white;
      text-align: center;
      font-weight: bold;
    }
  }

  nav {
    display: block;
    text-align: center;
    position: absolute;
    height: 210px;
    top: 50%;
    margin-top: -120px;
    width: 100%;

    a {
      font-size: 22px;
      color: #4d4d4e;
      display: block;
      line-height: 51px;
      height: 51px;
      position: relative;
      text-decoration: none;

      i {
        transition: all 0.3s ease-out;
      }

      &:hover {
        color: white;

        svg {
          opacity: 0;
        }

        &:after {
          opacity: 1;
        }
      }

      &:after {
        content: '';
        font-size: 9px;
        letter-spacing: 2px;
        position: absolute;
        bottom: 0;
        display: block;
        width: 100%;
        text-align: center;
        opacity: 0;
        -webkit-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
      }

      &:first-child {
        &:after {
          content: 'HOME';
        }
      }
    }

    a.about-link {
      &:after {
        content: 'ABOUT';
      }
    }

    a.contact-link {
      &:after {
        content: 'CONTACT';
      }
    }

    a.portfolio-link {
      &:after {
        content: 'PORTFOLIO';
      }
    }

    a.active {
      svg {
        color: white;
      }
    }
  }

  ul {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0;

    li {
      a {
        padding: 7px 0;
        display: block;
        font-size: 15px;
        line-height: 16px;
        color: #4d4d4e;

        svg:hover {
          color: white;
        }
      }
    }
  }
}

.hamburger-icon, .close-icon {
  display: none;
}

@media screen and (max-width: 1200px){
  #dark .mobile-show {
    background: black;
  }

  #light .mobile-show {
    background: mediumpurple;
    color: black;
  }

  #light .nav-bar nav a {
    color: black;
  }

  .nav-bar {
    background: transparent;
    height: auto;
    min-height: auto;
    position: relative;
    display: flex;
    width: 100vw;
    padding-left: 10px;

    ul, nav {
      display: none;
    }

    nav {
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: fixed;
      z-index: 3;
      margin: 0;

      a {
        font-weight: bold;
      }

      a:after {
        opacity: 1;
        padding: initial;
        width: auto;
        margin-left: 100px;
      }

      a svg {
        opacity: 1 !important;
      }

      &.mobile-show {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: unset;
      }
    }

    .hamburger-icon, .close-icon {
      display: block;
      position: absolute;
      top: 15px;
      right: 15px;
    }
  }
}





